const container=document.getElementById('root');
const content=document.createElement('div');

//1.Îç∞Ïù¥ÌÑ∞ Î∞õÏïÑÏò§Í∏∞
const ajax=new XMLHttpRequest(); 
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL ='https://api.hnpwa.com/v0/item/@id.json';
ajax.open('GET',NEWS_URL,false); 
ajax.send(); 
const newsFeed = JSON.parse(ajax.response);
const ul = document.createElement('ul'); 

//2.Ïù¥Î≤§Ìä∏ Îã¨Í∏∞
window.addEventListener('hashchange',function(){
    const id=location.hash.substr(1) //#ÏùÑÎπºÍ≥† idÍ∞íÎßå Í∞ÄÏ†∏Ïò§Î†§Í≥† 
    ajax.open('GET',CONTENT_URL.replace('@id',id),false); //@idÎ•º ÏúÑÏùò #Í∞íÏùÑÎ∫Ä idÎ°úÎ∞îÍøà
    ajax.send();
    const newsContent=JSON.parse(ajax.response);
    const title=document.createElement('h1');
    title.innerHTML=newsContent.title;//titleÏùò ÎÇ¥Ïö©ÏùÄ newsContentÎ°ú Í∞ÄÏ†∏Ïò® Í≥≥ÏóêÏÑú Í∞ÄÏ†∏Ïò§Î©¥Îê®
    content.appendChild(title);    });

//3.UI 
for(let i=0; i<10; i++){ 
    const div=document.createElement('div');
    const li=document.createElement('li');
    const a = document.createElement('a');
    //Îß§Î≤à ÏÉàÎ°ú ÎßåÎì§Ïñ¥ÏßÄÎãàÍπå forÎ¨∏ ÏïàÏóêÏÑú ÎßåÎì§Ïñ¥ÏïºÌï® 
    div.innerHTML = `
                    <li>
                        <a href="#${newsFeed[i].id}">
                        ${newsFeed[i].title} (${newsFeed[i].comments_count})</a>
                    </li>
                    ` 
    ul.appendChild(div.firstElementChild); //divÏùò Ï≤´Î≤àÏß∏ ÏûêÏãù liÎ•º ulÏóê append
    }

container.appendChild(ul);
container.appendChild(content);